      <h3 id="fortbildung">Fortbildung</h3>
      <p>
        Wenn du dich in deinem Beruf als MFA / ZFA weiter spezialisieren
        mchtest, fllt das unter den Begriff der Fortbildungen. Deine
        bisheriges Wissen in dem Beruf wird erweitert, angepasst, erhalten oder
        fr den weiteren Aufstieg ausgebaut. An einem Beispiel aus der Praxis,
        wird der Fall noch klarer:
      </p>
      <p>
        Du bist als Medizinische Fachangestellte in einer onkologischen Praxis
        ttig und mchtest mehr qualifizierte Aufgaben bei der onkologischen und
        palliativmedizinischen Versorgung der Patienten bernehmen. Die
        Fortbildung zur
        <b-link
          to="/karriere/fort-und-weiterbildung/ueberblick/onkologischer-fachassistentin"
          >Fachassistentin Onkologie</b-link
        >
        macht dich zur Spezialistin dafr. Du erweiterst dein Wissen im Beruf
        der MFA. Durch deine zustzliche Qualifizierung steigt auch dein Gehalt.
        Ein anderes Beispiel hierfr sind Rntgenscheine. Durch regelmige
        Fortbildungen bleibt dein Wissen ber die Rntgen- und Strahlenheilkunde
        erhalten und wird aufgefrischt, um die Qualitt deiner Arbeit auf
        gewohnt hohem Niveau zu halten. Auerdem bleibst auf dem Laufenden, wenn
        es Neuerungen auf dem Gebiet gibt.
      </p>
      <p>
        Die meisten Fortbildungen werden von dem Arbeitgeber initiiert, weil es
        fr sie von Vorteil ist eine Medizinische Fachangestellte oder
        Zahnmedizinische Fachangestellte mit einer speziellen
        Zusatzqualifikation zu haben und werden deshalb auch von ihm bernommen.
        Zudem sind bestimmte Fortbildungen pflichtgem in regelmigen
        Abstnden aufzufrischen.
      </p>

      <h3 id="weiterbildung">Weiterbildung</h3>
      <p>
        Dagegen vermitteln Weiterbildungen eher allgemeine Inhalte und sind
        darauf ausgelegt, dass du zustzliche Qualifikationen als MFA / ZFA
        erwirbst. Erste Vorbildungen, die du mit deiner Ausbildung zur
        Medizinischen Fachangestellten bzw. Zahnmedizinischen Fachangestellten
        erhalten hast, werden erweitert oder vertieft. Dazu knntest du dir
        einen EKG-Kurs vorstellen, bei dem die verschiedenen Bilder von
        Herzrhythmusstrungen erlutert werden und deine Teilnahme mit einer
        anschlieenden Teilnahmebescheinigung belegt wird. Dein Wissen wird bei
        dem Kurs vertieft und erweitert. Eine Zusatzqualifikation oder einen
        zustzlichen Titel erlangst du dadurch aber nicht.
      </p>
      <p>
        Mit Weiterbildungen knnen auch erweiterte Bildungsabschlsse nachgeholt
        werden. Dazu zhlt z. B. der Besuch einer Abendschule, um das Abitur
        nach zu machen oder
        <a
          href="https://www.sgd.de/kursseite/gepr-fachwirtin-im-gesundheits-und-sozialwesen-ihk.html?referrer=pn_13_45"
          target="_blank"
          >Fernstudiengnge</a
        >, wie der
        <b-link
          to="/karriere/fort-und-weiterbildungen/fachwirtin-im-gesundheits-und-sozialwesen"
          >Fachwirt im Gesundheits- und Sozialwesen</b-link
        >.
      </p>
      <p>
        Auch Umschulungen, die dir zu beruflichen Qualifizierungen in einem ganz
        neuen Beruf verhelfen, zhlen zu den Weiterbildungen.
      </p>
      <p>
        Das Wissen wird oft in relativ kurzer Zeit in Kursen oder Seminaren
        vermittelt.
      </p>
      <h3 id="studium">Studium</h3>
      <p>
        Ein Studium verhilft dir zu einem akademischen Grad. Es ist sehr
        theoretisch ausgelegt und befhigt dich zu wissenschaftlichen Arbeiten.
        Viele Studiengnge knnen heute auch ohne Abitur absolviert werden, wie
        z.B. das
        <b-link to="/karriere/fort-und-weiterbildung/ueberblick/medizinstudium"
          >Medizinstudium</b-link
        >. Voraussetzungen fr die Studienzulassung sind meist eine
        abgeschlossene Berufsausbildung und eine gewisse Anzahl an Berufsjahren.
        Heute gibt es viele Fernstudium-Anbieter, die es dir als MFA / ZFA
        ermglichen neben dem Beruf zu studieren und einen Studienabschluss
        erwerben. Ein akademischer Abschluss ffnet dir neue Tren in
        unterschiedlichen Sparten des Gesundheitswesens und verhilft zu besserem
        Gehalt. Viele Arbeitgeber fordern ein abgeschlossenes Studium fr
        Fhrungspositionen.
      </p>
      <p>
        Ein Studium ist zeitintensiv. Es dauert ca. 3 bis 3,5 Jahre. Auch das
        Lernpensum ist hher als bei Fort- oder Weiterbildungen. Es werden
        aufwndige Projekt- und Seminararbeiten gefordert. Und das in jedem
        Semester. Studieren bedeutet auch sich Lerninhalte selbst zu erarbeiten
        und Lernprozesse selbst zu steuern. Die Lernstruktur und Vorgaben bei
        Fort- und Weiterbildungen sind vorgegeben, das Lernen ist hnlich wie
        damals in der Schule.
      </p>
      <p>
        Ich will dir das Studieren nicht madig machen. Ich spreche aus Erfahrung
        und wei, wie anstrengend ein berufsbegleitendes Studium sein kann. Aber
        ich kann dir auch sagen: es lohnt sich. Fr deine berufliche und
        persnliche Entwicklung auf jeden Fall.
      </p>
