      <h3 id="das-richtige-fuer-dich">Was ist das Richtige fr Dich?</h3>
      <p>
        Was fr dich die richtige Weiterbildungsform ist, hngt davon ob, welche
        beruflichen Ziele du hast. Was mchtest du im Laufe der Jahre noch
        erreichen?
      </p>
      <p>
        Wenn du MFA / ZFA mit Leib und Seele bist und auch bleiben mchtest,
        knnte eine Fortbildung fr dich das Richtige sein. Du kannst dich in
        einem medizinischen Bereich spezialisieren und verantwortungsvollere
        Aufgaben bernehmen. Durch die Zusatzqualifikation kannst du mehr Gehalt
        erlangen. Das hat mit den
        <b-link to="/karriere/mfa/gehalt#taetigkeitsgruppen"
          >Ttigkeitsgruppen</b-link
        >
        zu tun. Lese dir hierzu unseren Artikel ber
        <b-link to="/karriere/mfa/gehalt"
          >Gehalt fr Medizinische Fachangestellte</b-link
        >
        bzw.
        <b-link to="/karriere/zfa/gehalt"
          >Gehalt fr Zahnmedizinische Fachangestellte</b-link
        >.
      </p>
      <p>
        Vielleicht mchtest du auch auf lange Sicht raus aus der Praxis und mehr
        verwaltungsbezogene Aufgaben erledigen. Dann knnte die Fortbildung zum
        <b-link
          to="/karriere/fort-und-weiterbildung/ueberblick/fachwirtin-im-gesundheits-und-sozialwesen"
          >Fachwirt im Gesundheits- und Sozialwesen</b-link
        >
        etwas fr dich sein.
      </p>
      <p>
        Wenn du zufrieden mit deiner jetzigen Ttigkeit und Position in der
        Praxis bist, solltest du in deinem eigenen Interesse dazu bereit sein,
        dich regelmig fortzubilden. Gerade in unserer Branche gibt es stndig
        neue medizinische Techniken, genderte Vorgaben von den Krankenkassen
        bzw. Kassenrztlichen Vereinigungen oder neue Gesetze, die unsere
        alltgliche Arbeit betreffen. Hier gilt es auf dem Laufenden zu bleiben.
      </p>
      <p>
        Natrlich solltest du auch den Zeitfaktor nicht auer Acht lassen.
        Zustzlich zu Familie und Kindern kann ein Studium zur groen
        Herausforderung werden.
      </p>
      <p>
        PS: Fr viele Bildungsmanahmen gibt es
        <b-link to="/karriere/fort-und-weiterbildung#foerderung-finanzierung"
