        fluid
      />
      <p class="h3">
        Welche Mglichkeit hast du als MFA & ZFA, um deine Weiterbildung zu
        finanzieren?
      </p>
      <h3 id="chef-fragen" class="bold">Beim Chef nachfragen</h3>
      <p>
        Das ist in vielen Situationen die einfachste und unkomplizierteste
        Lsung. Es kommt dabei aber auf die Art deiner
        <b-link to="/karriere/fort-und-weiterbildung/ueberblick"
          >Weiterbildung</b-link
        >
        an. Angenommen, du arbeitest in einer chirurgischen Praxis, in der auch
        operiert wird. Bisher hast du hauptschlich den laufenden
        Sprechstundenbetrieb organisiert. Dich interessiert aber die Arbeit im
        OP sehr und du schlgst deinem Chef vor, die
        <b-link
          to="/karriere/fort-und-weiterbildung/ueberblick/ambulantes-operieren"
          >Fortbildung Ambulantes Operieren</b-link
        >
        zu machen. Davon wrde dein Chef sicherlich enorm profitieren, sodass er
        ein echtes Interesse an deiner beruflichen Weiterbildung hat. Unter
        diesen Umstnden wrde er die Kosten der Fortbildung bestimmt tragen.
      </p>
      <p>
        In manchen Fllen, besonders wenn die Kursgebhren in den
        Tausender-Bereichen liegen, wollen einige Arbeitgeber, dass du dich fr
        eine gewisse Dauer dazu verpflichtest in der Praxis zu bleiben. Im
        Arbeitsrecht nennt man das, die Rckzahlungsklausel. Dafr mssen einige
        Voraussetzungen erfllt sein, damit dein Arbeitgeber diese Klausel
        anwenden darf. Welche das sind und wie hoch die Bindungsdauer je nach
        Fortbildungsdauer ist, findest du z. B.
        <a
          href="https://www.arbeitsrecht-weltweit.de/2018/03/01/rueckzahlung-von-fortbildungskosten-was-ist-moeglich/"
          target="_blank"
          >hier</a
        >.
      </p>
      <p>
        Falls du dich beruflich umorientieren mchtest und eine Weiterbildung im
        Auge hast, die deiner Praxis keine Vorteile bringt, wird es schwierig
        werden, dass dein Chef sich an den Kosten beteiligen wird.
      </p>
      <p>
        Dafr bieten der Staat und die einzelnen Bundeslnder einige
        Mglichkeiten, um dich zu untersttzen.
      </p>
      <h3 id="bafoeg" class="bold">Aufstiegs-BAfG</h3>
      <p>
        Das Aufstiegs-BAfG, frher auch Meister-BAfG genannt, untersttzt
        Arbeitnehmer, Berufsrckkehrer und Selbststndige, die sich per
        Aufstiegsfortbildung weiterbilden mchten. Unter einer
        Aufstiegsfortbildung versteht man eine Fortbildung, die auf dem
        Ausbildungsberuf aufbaut und die dich fr einen greren
        Verantwortungsbereich mit hherem Gehalt qualifiziert. Frderungsfhig
        wre z. B. die Fortbildung zum
        <b-link
          to="/karriere/fort-und-weiterbildung/ueberblick/fachwirtin-im-gesundheits-und-sozialwesen"
          >Fachwirt im Sozial- und Gesundheitswesen</b-link
        >. Das Aufstiegs-BAfG gilt auch Fernstudiengnge.
      </p>
      <p>
        Die Frderung beinhaltet Zuschsse, die du nicht zurck zahlen musst.
        Auerdem besteht die Mglichkeit, ein zinsgnstiges Darlehen bei der
        Kreditanstalt fr Wiederaufbau (KfW) ber die Differenz zwischen
        Zuschussanteil und maximalem Frderbetrag abzuschlieen (hnlich wie ein
        Bildungskredit).
      </p>
      <p>
        Alle Informationen fr den
        <a
          href="https://www.aufstiegs-bafoeg.de/de/was-wird-gefoerdert-1698.html"
          target="_blank"
          >Anspruch auf Aufstiegs-BAfG</a
        >, die Beantragung sowie Antragsformulare und Kontaktdaten zu den
        jeweiligen Beratungsstellen der Bundeslnder findest du auf der
        Internetseite des Bundesministerium fr Bildung und Forschung.
      </p>
      <h3 id="bildungsgutschein" class="bold">Bildungsgutschein</h3>
      <p>
        Mit dem
        <a
          href="https://www.arbeitsagentur.de/karriere-und-weiterbildung/bildungsgutschein"
          target="_blank"
          >Bildungsgutschein </a
        >frdert die Bundesagentur fr Arbeit die berufliche Weiterbildung von
        Arbeitslosen, Berufsttigen und Berufsrckkehrern. Das Ziel ist, weitere
        berufliche Qualifikationen zu erlagen, um nach lngerer Berufsauszeit
        wieder leichter in den Beruf einzusteigen. Fehlende Abschlsse knnen
        mit dem Bildungsgutschein ebenso nachgeholt werden, wie Lehrgnge, die
        einer drohenden Arbeitslosigkeit entgegenwirken sollen. Wenn die
        zustndige Agentur fr Arbeit eine Weiterbildung fr notwendig hlt,
        bekommst du den Bildungsgutschein ausgehndigt. Dieser kann bei einem
        zugelassen Anbieter eingelst werden. bernommen werden neben den
        Lehrgangskosten auch Kosten zum Beispiel fr Prfungen, Bcher, Fahrten,
        Unterkunft und Verpflegung oder Kinderbetreuung. Welche Bildungsziele
        die Agentur fr Arbeit frdert, wird jedes Jahr wieder neu und nach
        Region festgelegt.
      </p>
      <div class="example shadow1 my-4">
        <p class="lead bold">MFA mal anders  Tipp</p>
        <p>
          Ich knnte mir vorstellen, dass der Bildungsgutschein gerade fr
          Mtter interessant sein knnte, die nach lngerer Elternzeit wieder
          zurck in den Beruf der MFA oder ZFA mchten. Im Gesundheitswesen
          ndert sich in wenigen Jahren so viel, von Abrechnungsfragen ber
          (zahn)medizinische Techniken, sodass man den Bildungsgutschein gut
          nutzen knnte, um wieder auf dem Laufenden zu sein.
        </p>
      </div>
      <p>
        Ob du
        <a
          href="https://www.arbeitsagentur.de/bildungsgutschein"
          target="_blank"
          >Anspruch auf einen Bildungsgutschein</a
        >
        hast, kann dir deine zustndige Agentur fr Arbeit nach entsprechender
        Beratung mitteilen.
      </p>
      <h3 id="bildungsprmie" class="bold">
        Prmiengutschein (Bildungsprmie)
      </h3>
      <p>
        Der
        <a href="https://www.bildungspraemie.info/" target="_blank"
          >Prmiengutschein </a
        >ist fr Arbeitnehmer und Selbststndige, deren zu versteuerndes
        Jahreseinkommen 20.000 Euro bzw. 40.000 Euro bei Verheirateten nicht
        bersteigt. Auch Arbeitnehmer im Mutterschutz und in Eltern- bzw.
        Pflegezeit knnen den Prmiengutschein erhalten.
      </p>
      <p>
        Der Staat beteiligt sich mit maximal 500 Euro an Kosten, um eine
        Weiterbildung zu finanzieren. In den Bundeslndern Brandenburg,
        Rheinland-Pfalz, Sachsen, Sachsen-Anhalt und Schleswig-Holstein darf der
        Prmiengutschein nur fr Weiterbildungen genutzt werden, die
        <strong>maximal 1.000 Euro</strong> inklusive Mehrwertsteuer kosten.
        Alle anderen Bundeslndern rechnen den Prmiengutschein fr
        Weiterbildungen <strong>auch</strong> <strong>ab 1.000 Euro</strong> an.
        Relevant ist hier nicht dein Wohnort, sondern das Bundesland, indem die
        Weiterbildung stattfindet.
      </p>
      <p>
        Die berufliche Weiterbildung darf nicht im Zusammenhang mit einer
        betrieblichen Schulung stattfinden. Also zum Beispiel kann er nicht
        angewendet werden fr eine Hygieneschulung, die in der Praxis
        stattfindet.
      </p>
      <p>
        Der Prmiengutschein kann jhrlich neu beantragt werden. So knntest du
        dir jedes Jahr eine Weiterbildung mithilfe des Prmiengutscheins
        finanzieren.
      </p>
      <p>
        Alle
        <a href="https://www.bildungspraemie.info/" target="_blank"
          >Infos zur Bildungsprmie</a
        >
        findest du auf der Webseite des Bundesministerium fr Bildung und
        Forschung.
      </p>

      <h3 id="bildungskredit" class="bold">Bildungskredit</h3>
      <p>
        Der Bildungskredit untersttzt bei schulischen Ausbildungen und bei
        einem Studium.
      </p>
      <p>
        Mit einem zinsgnstigem Darlehen finanziert ist der Bildungskredit
        staatlich begnstigt und kann unabhngig von deinen Einkommens- und
        Vermgensverhltnissen oder die deines Ehepartners, deines
        Lebenspartners oder deiner Eltern aufgenommen werden. Die Beantragung
        kann relativ einfach ber ein
        <a
          href="https://www.bva.bund.de/DE/Services/Buerger/Schule-Ausbildung-Studium/Bildungskredit/Antrag/Bildungskredit-online/bildungskredit-online.html?nn=149374"
          target="_blank"
          >Online-Formular</a
        >
        abgewickelt werden. Das begnstigte Kreditvolumen betrgt zwischen 1.000
        und 7.200 Euro und wird wahlweise bis zu 24 Monatsraten in Hhe von 100
        EUR, 200 EUR oder 300 EUR oder auf Wunsch in einer Einmalzahlung von bis
        zu 3.600 EUR fr ausbildungsbezogene Aufwendungen ausgezahlt. Es kann
        sogar mit anderen Finanzierungsangeboten  wie BAfG  kombiniert
        werden. Die Rckzahlungsrate in Hhe von 120 EUR monatlich beginnt vier
        Jahre nach der ersten Ratenzahlung.
      </p>
      <p>
        Weitere
        <a
          href="https://www.bva.bund.de/DE/Services/Buerger/Schule-Ausbildung-Studium/Bildungskredit/bildungskredit_node.html"
          target="_blank"
          >Informationen zum Bildungskredit</a
        >
        findest du auf der Seite des Bundesverwaltung.
      </p>

      <h3 id="aufstiegsstipendium" class="bold">Aufstiegsstipendium</h3>
      <p>
        Warst du eine besonders gute Schlerin whrend der Ausbildung? Und bist
        immer noch besonders motiviert und mchtest dich beruflich durch ein
        Studium weiterqualifizieren? Dann knnte das Aufstiegsstipendium des
        Bundesministeriums fr Bildung und Forschung fr dich in Frage kommen.
        Es richtet sich an Fachkrfte, die nach der Ausbildung bereits
        mindestens zwei Jahre Berufserfahrung haben.
      </p>
      <p>
        Voraussetzungen dafr ist ein Nachweis ber sehr gute Leistungen in
        Ausbildung und Beruf, u.a. durch die Note der Berufsabschlussprfung
        oder der Abschlussprfung einer Aufstiegsfortbildung (Gesamtergebnis mit
        mindestens Note 1,9 oder 87 Punkte und mehr). Eine weitere Mglichkeit
        ist die besonders erfolgreiche Teilnahme an einem berregionalen
        beruflichen Leistungswettbewerb (Platzierung unter den ersten drei).
        Sollten die ersten beiden Voraussetzungen fr dich nicht zutreffen kann
        auch ein begrndeter Vorschlag des Arbeitgebers fr die Bewerbung um ein
        Aufstiegsstipendium qualifizieren.
      </p>
      <p>
        Alle Informationen zum Aufstiegsstipendium sowie zu die Beantragung
        kannst du auf der
        <a
          href="https://www.sbb-stipendien.de/aufstiegsstipendium.html"
          target="_blank"
          >Webseite der Stiftung Begabtenfrderungswerk berufliche Bildung</a
        >
        finden.
      </p>

      <h3 id="weiterbildungsstipendium" class="bold">
        Weiterbildungsstipendium
      </h3>
      <p>
        Das Weiterbildungsstipendium ist hnlich wie das Aufstiegsstipendium. Es
        richtet sich an Berufsttige, die mithilfe einer Weiterbildung ihre
        Fertigkeiten und Kenntnisse spezialisieren wollen. Die Weiterbildung
        kann fachlich sein, also auf deinen Beruf aufbauen, wie z. B. der
        Fachwirt oder aber fachbergreifend. Damit sind beispielsweise
        berufsbegleitende Studiengnge gemeint.
      </p>
      <p>
        Die Voraussetzungen sind die gleichen wie bei dem Aufstiegsstipendium.
        Allerdings muss man bei Aufnahme in das Programm unter 25 Jahre sein.
        Die Bewerbung richtest du in dem Fall an die rztekammer, bei die damals
        deinen Ausbildungsvertrag gegengezeichnet hat.
      </p>
      <p>
        Weitere Infos zum Weiterbildungsstipendium findest du wieder bei der
        <a
          href="https://www.sbb-stipendien.de/weiterbildungsstipendium/programmziele.html"
          target="_blank"
          >Stiftung Begabtenfrderungswerk berufliche Bildung</a
        >.
      </p>

      <h3 id="steuererklaerung" class="bold">
        Kostenerstattung ber die Steuererklrung
      </h3>
      <p>
        Die Kosten fr eine Weiterbildung kannst du auch von der Steuer
        absetzen. Bis zu 1000 Euro erkennt das Finanzamt bei Arbeitnehmern
        pauschal als Werbungskosten an. Wie hoch deine Rckzahlung ausfllt,
        hngt davon ab ,wie das Finanzamt deine Weiterbildung einordnet. Du
        kannst die konkret zuordenbaren Seminar- bzw. Studiengebhren, die
        Kosten fr notwendige Literatur und Arbeitsmaterialien sowie Reisekosten
        (Fahrt, bernachtung, Verpflegung etc.) geltend machen.
      </p>

      <h3 id="qualifizierungsoffensive" class="bold">
        WEITER.BILDUNG!  die Qualifizierungsoffensive
      </h3>
      <p>
        Die Bundesagentur fr Arbeit hat eine Qualifizierungsoffensive ins Leben
        gerufen von der vor allem kleine Unternehmen mit weniger als 10
        Mitarbeiter profitieren. Weiterbildungskosten werden hier sogar zu 100 %
        erstattet.
      </p>
      <p>
        Alle Informationen unter welchen Voraussetzungen dein Unternehmen von
        der Qualifizierungsoffensive profitiert, findest du auf der Seite der
        <a
          href="https://www.arbeitsagentur.de/unternehmen/finanziell/foerderung-von-weiterbildung"
          target="_blank"
          >Bundesagentur fr Arbeit</a
        >.
      </p>
      <p class="mt-4">
        Das waren 9 Mglichkeiten in der Kurzbersicht, die dir eine finanzielle
        Hilfe sein knnen, um in deine Karriere als MFA oder ZFA zu investieren.
        Natrlich sind nicht fr jeden die 9 Mglichkeiten der Finanzierung
        nutzbar, um deine Weiterbildung zu finanzieren. Dies soll fr dich
        lediglich ein berblick ber deine eventuellen Chancen auf die
        Reduzierung von Weiterbildungskosten sein. Welche Form der Frderung fr
        dich in Frage kommen knnte, findest du heraus, wenn du dich bei den
        einzelnen Stellen direkt informierst.
      </p>
      <p>
        Hast du bereits Erfahrung mit einer der 9 Frderungen gemacht und kannst
        erzhlen, wie das genau abgelaufen ist? Oder kennst du noch eine andere
        Mglichkeit, die hier noch nicht aufgefhrt ist?
        <b-link to="/kontakt">Lass es uns wissen</b-link>.
      </p>
    </div>

