ird der Fall noch klarer: ")]),_c('p',[_vm._v(" Du bist als Medizinische Fachangestellte in einer onkologischen Praxis ttig und mchtest mehr qualifizierte Aufgaben bei der onkologischen und palliativmedizinischen Versorgung der Patienten bernehmen. Die Fortbildung zur "),_c('b-link',{attrs:{"to":"/karriere/fort-und-weiterbildung/ueberblick/onkologischer-fachassistentin"}},[_vm._v("Fachassistentin Onkologie")]),_vm._v(" macht dich zur Spezialistin dafr. Du erweiterst dein Wissen im Beruf der MFA. Durch deine zustzliche Qualifizierung steigt auch dein Gehalt. Ein anderes Beispiel hierfr sind Rntgenscheine. Durch regelmige Fortbildungen bleibt dein Wissen ber die Rntgen- und Strahlenheilkunde erhalten und wird aufgefrischt, um die Qualitt deiner Arbeit auf gewohnt hohem Niveau zu halten. Auerdem bleibst auf dem Laufenden, wenn es Neuerungen auf dem Gebiet gibt. ")],1),_c('p',[_vm._v(" Die meisten Fortbildungen werden von dem Arbeitgeber initiiert, weil es fr sie von Vorteil ist eine Medizinische Fachangestellte oder Zahnmedizinische Fachangestellte mit einer speziellen Zusatzqualifikation zu haben und werden deshalb auch von ihm bernommen. Zudem sind bestimmte Fortbildungen pflichtgem in regelmigen Abstnden aufzufrischen. ")]),_c('h3',{attrs:{"id":"weiterbildung"}},[_vm._v("Weiterbildung")]),_c('p',[_vm._v(" Dagegen vermitteln Weiterbildungen eher allgemeine Inhalte und sind darauf ausgelegt, dass du zustzliche Qualifikationen als MFA / ZFA erwirbst. Erste Vorbildungen, die du mit deiner Ausbildung zur Medizinischen Fachangestellten bzw. Zahnmedizinischen Fachangestellten erhalten hast, werden erweitert oder vertieft. Dazu knntest du dir einen EKG-Kurs vorstellen, bei dem die verschiedenen Bilder von Herzrhythmusstrungen erlutert werden und deine Teilnahme mit einer anschlieenden Teilnahmebescheinigung belegt wird. Dein Wissen wird bei dem Kurs vertieft und erweitert. Eine Zusatzqualifikation oder einen zustzlichen Titel erlangst du dadurch aber nicht. ")]),_c('p',[_vm